"\n                            @click=\"increaseHeight = true\"\n                            class=\"guest-noti-view-more\"\n                        >\n                            View More\n                        </div>\n                        <div\n                            v-if=\"\n                                increaseHeight == true &&\n                                notifications.length > 0 &&\n                                (show == 'notifications' || screenWidth >= 631)\n                            \"\n                            @click=\"increaseHeight = false\"\n                            class=\"guest-noti-view-more\"\n                        >\n                            View Less\n                        </div>\n                    </div>\n                    <!-- <div v-if=\"screenWidth > 631\" class=\"host-explore-vendor\">\n                        <img src=\"/assets/hosthome/vendor.svg\" alt=\"\" />\n                        <h3>Explore vendors near you</h3>\n                        <p>starting from ₹2000 for couple plan book now</p>\n                        <a href=\"/directory\">Explore Vendors</a>\n                    </div> -->\n                </div>\n            </div>\n        </div>\n        <div class=\"host-home-right-cont\">\n            <div\n                v-if=\"screenWidth > 631 && todaySchedule.length > 0\"\n                class=\"host-today-task\"\n            >\n                <h1>Today’s Task</h1>\n                <div>\n                    <div\n                        v-for=\"item in todaySchedule\"\n                        :key=\"item.id\"\n                        class=\"host-today-task-single\"\n                    >\n                        <div class=\"host-today-task-single-head\">\n                            <span>\n                                <span></span>\n                                {{ item.taskStatus }}\n                            </span>\n                            <span>{{ item.category.name }}</span>\n                        </div>\n                        <div class=\"host-today-task-assign\">\n                            Assigned to\n                            <span>: {{ item.assignable.name }}</span>\n                        </div>\n                        <p>\n                            {{ item.title }}\n                        </p>\n                        <div class=\"host-today-schedule\">\n                            <span\n                                ><img\n                                    src=\"/assets/icon-calendar.svg\"\n                                    alt=\"\"\n                                />{{ item.startDate | moment(\"Do MMMM, YYYY\") }}\n                                -\n                                {{\n                                    item.completionDate\n                                        | moment(\"Do MMMM, YYYY\")\n                                }}</span\n                            >\n                            <span>\n                                <img src=\"/assets/icon-clock.svg\" alt=\"\" />{{\n                                    new Date(\"1970-01-01T\" + item.startTime)\n                                        | moment(\"h:mm a\")\n                                }}\n                                -\n                                {{\n                                    new Date(\n                                        \"1970-01-01T\" + item.completionTime\n                                    ) | moment(\"h:mm a\")\n                                }}</span\n                            >\n                            <span\n                                ><img src=\"/assets/tag.svg\" alt=\"\" />{{\n                                    item.taskPriority\n                                }}\n                                Priority</span\n                            >\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div class=\"host-cal-events-cont\">\n                <div\n                    @click=\"\n                        show == 'upcomingEvents'\n                            ? (show = 'null')\n                            : (show = 'upcomingEvents')\n                    \"\n                    class=\"host-cal-head\"\n                >\n                    <div>\n                        <div>Upcoming tasks</div>\n                        <div>Don’t miss the upcoming tasks</div>\n                    </div>\n                    <!-- <a\n                        v-if=\"screenWidth > 631\"\n                        :href=\"route('hosttasks.index')\"\n                    >\n                        +\n                    </a> -->\n                    <img\n                        v-if=\"screenWidth <= 631 && show != 'inviStatus'\"\n                        src=\"/assets/guestDash/chevron-down.svg\"\n                        alt=\"\"\n                    />\n                    <img\n                        v-if=\"screenWidth <= 631 && show == 'inviStatus'\"\n                        src=\"/assets/guestDash/chevron-up.svg\"\n                        alt=\"\"\n                    />\n                </div>\n                <div v-show=\"show == 'upcomingEvents' || screenWidth >= 631\">\n                    <div class=\"cal-cont\">\n                        <Calendar\n                            :attributes=\"attr\"\n                            :masks=\"{ title: 'MMMM' }\"\n                            @dayclick=\"dayclicked\"\n                        >\n                            <template #day-popover=\"{ dayTitle }\">\n                                <div\n                                    class=\"text-xs text-gray-300 font-semibold text-center\"\n                                >\n                                    {{ dayTitle }}\n                                </div>\n                                <div\n                                    v-for=\"(item, index) in todos\"\n                                    :key=\"index\"\n                                >\n                                    <div\n                                        v-if=\"\n                                            `'` +\n                                                new Date(item.startDate) +\n                                                `'` ==\n                                            `'` + new Date(date.id) + `'`\n                                        \"\n                                    >\n                                        <div>\n                                            {{ item.title }}\n                                        </div>\n                                    </div>\n                                </div>\n                            </template>\n                        </Calendar>\n                    </div>\n                    <div v-if=\"tasks.length > 0\" class=\"host-all-events\">\n                        <div\n                            v-for=\"(item, index) in tasks\"\n                            :key=\"index\"\n                            class=\"host-today-task-single\"\n                        >\n                            <div class=\"host-today-task-single-head\">\n                                <span class=\"event-cal\">\n                                    <span\n                                        style=\"background: #f4a545 !important\"\n                                    ></span>\n                                    {{\n                                        item.startDate | moment(\"Do MMMM, YYYY\")\n                                    }}\n                                </span>\n                                <span class=\"event-time\">{{\n                                    new Date(\"1970-01-01T\" + item.startTime)\n                                        | moment(\"h:mm a\")\n                                }}</span>\n                                <span>{{ item.category.name }}</span>\n                            </div>\n                            <div class=\"host-today-task-assign\">\n                                Assigned to\n                                <span>: {{ item.assignable.name }}</span>\n                            </div>\n                            <p>\n                                {{ item.title }}\n                            </p>\n                            <div\n                                class=\"host-today-schedule\"\n                                style=\"justify-content: flex-end\"\n                            >\n                                <span\n                                    ><img src=\"/assets/tag.svg\" alt=\"\" />{{\n                                        item.taskPriority\n                                    }}\n                                    Priority</span\n                                >\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"no-tasks\" v-else>No Tasks Added yet!</div>\n                </div>\n            </div>\n        </div>\n        <!-- <div\n            v-if=\"screenWidth < 631\"\n            class=\"host-explore-vendor\"\n            style=\"margin: auto\"\n        >\n            <img src=\"/assets/hosthome/vendor.svg\" alt=\"\" />\n            <h3>Explore vendors near you</h3>\n            <p>starting from ₹2000 for couple plan book now</p>\n            <a href=\"/directory\">Explore Vendors</a>\n        </div> -->\n    </div>\n</template>\n\n<script>\nimport Calendar from \"v-calendar/lib/components/calendar.umd\";\n\nexport default {\n    components: {\n        Calendar,\n    },\n    props: [\n        \"loggedIn\",\n        \"invitation\",\n        \"invitationsaccepted\",\n        \"invitationsdeclined\",\n        \"invitationspending\",\n        \"notifications\",\n        \"invitationcount\",\n        \"accaccomodation\",\n        \"allaccomodation\",\n        \"sentaccomodation\",\n        \"sentlogistics\",\n        \"acclogisitcs\",\n        \"alllogisitcs\",\n        \"decaccomodation\",\n        \"declogisitcs\",\n        \"guestcount\",\n        \"members\",\n        \"tasks\",\n    ],\n    data() {\n        return {\n            todos: this.tasks,\n            screenWidth: window.screen.width,\n            increaseHeight: false,\n            show: null,\n            weekDays: [\n                \"Sunday\",\n                \"Monday\",\n                \"Tuesday\",\n                \"Wednesday\",\n                \"Thursday\",\n                \"Friday\",\n                \"Saturday\",\n            ],\n            months: [\n                \"January\",\n                \"February\",\n                \"March\",\n                \"April\",\n                \"May\",\n                \"June\",\n                \"July\",\n                \"August\",\n                \"September\",\n                \"October\",\n                \"November\",\n                \"December\",\n            ],\n            date: {\n                name: \"\",\n                id: \"\",\n                day: new Date().getDate(),\n                week: new Date().getDay(),\n                year: new Date().getFullYear(),\n                month: new Date().getMonth(),\n            },\n        };\n    },\n    methods: {\n        dayclicked(day) {\n            this.date.id = day.id;\n            this.date.day = day.day;\n            this.date.month = day.month - 1;\n            this.date.year = day.year;\n            this.date.week = day.weekday - 1;\n            this.date.name =\n                this.weekDays[day.weekday - 1] +\n                \" \" +\n                this.date.day +\n                \", \" +\n                this.months[day.month - 1] +\n                \" \" +\n                this.date.year;\n        },\n        setWidth() {\n            this.screenWidth = window.screen.width;\n        },\n        countDown() {\n            const fixedDate = this.invitation.startDate;\n            //console.log(fixedDate);\n            const deadlineTimer = document.querySelectorAll(\n                \".guest-banner-timer-single > span:nth-child(1)\"\n            );\n\n            const futureDate = new Date(fixedDate);\n\n            const futureTime = futureDate.getTime();\n\n            function getRemainingTime() {\n                let today = new Date().getTime();\n\n                let totalRemainingMS = futureTime - today;\n                if (totalRemainingMS < 0) {\n                    clearInterval(countdown);\n                    return;\n                }\n\n                //values in ms\n                const Oneday = 24 * 60 * 60 * 1000;\n                const Onehour = 60 * 60 * 1000;\n                const Oneminutes = 60 * 1000;\n\n                //remaining time calulation\n                let Day = Math.floor(totalRemainingMS / Oneday);\n                let Hour = Math.floor((totalRemainingMS % Oneday) / Onehour);\n                let Minutes = Math.floor(\n                    (totalRemainingMS % Onehour) / Oneminutes\n                );\n                let Seconds = Math.floor(\n                    (totalRemainingMS % Oneminutes) / 1000\n                );\n\n                const value = [Day, Hour, Minutes, Seconds];\n\n                function format(item) {\n                    if (item < 10) {\n                        return (item = `0${item}`);\n                    } else {\n                        return item;\n                    }\n                }\n                deadlineTimer.forEach(function (item, index) {\n                    item.innerHTML = format(value[index]);\n                });\n            }\n            let countdown = setInterval(getRemainingTime, 1000);\n            getRemainingTime();\n        },\n    },\n    computed: {\n        attr() {\n            return [\n                // Attributes for todos\n                ...this.todos.map((todo) => ({\n                    dates: todo.startDate,\n                    highlight: {\n                        style: {\n                            backgroundColor: \"#C4456F\",\n                        },\n                        contentStyle: {\n                            color: \"#FFFFFF\",\n                        },\n                    },\n                    // popover: true,\n                    popover: {\n                        isInteractive: true,\n                        visibility: \"click\",\n                    },\n                    customData: todo,\n                })),\n                // {\n                //     key: \"today\",\n                //     highlight: {\n                //         style: {\n                //             backgroundColor: \"#EB5757\",\n                //         },\n                //     },\n                //     dates: new Date(),\n                // },\n            ];\n        },\n        todaySchedule() {\n            let todayevents = [];\n            let today = new Date().toDateString();\n            this.todos.forEach((item) => {\n                if (\n                    '\"' + new Date(item.startDate).toDateString() + '\"' ==\n                    '\"' + today + '\"'\n                ) {\n                    todayevents.push(item);\n                }\n            });\n            return todayevents;\n        },\n    },\n    mounted() {\n        this.$nextTick(() => {\n            window.addEventListener(\"resize\", this.setWidth);\n            this.countDown();\n        });\n    },\n};\n</script>\n\n<style scoped>\n.host-home-main-cont {\n    display: flex;\n    flex-wrap: nowrap;\n    background: #ffffff;\n}\n.host-home-left-cont {\n    width: 70%;\n    background: #f4f3fa;\n    padding: 12px;\n}\n.host-home-left-cont > h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 18.8093px;\n    line-height: 28px;\n    color: #000000;\n    padding-left: 15px;\n    margin-bottom: 15px;\n}\n.host-banner-cont {\n    position: relative;\n    padding-top: 210px;\n    margin-bottom: 15px;\n}\n.no-invi-banner-cont {\n    position: relative;\n    margin-bottom: 15px;\n    background: #ffffff;\n    border-radius: 10px;\n    padding-top: 210px;\n}\n.no-invi-banner-cont > img {\n    top: 30px;\n    right: 30px;\n    position: absolute;\n    width: 25%;\n}\n.no-invi-content {\n    top: 30px;\n    left: 30px;\n    position: absolute;\n    width: 60%;\n}\n.no-invi-content > h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 22px;\n    line-height: 33px;\n    color: #b52f6f;\n    margin-bottom: 10px;\n}\n.no-invi-content > p {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 24px;\n    color: #000000;\n    margin-bottom: 7px;\n}\n.no-invi-content > a {\n    border: none;\n    outline: none;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 22px;\n    background: #b52f6f;\n    border-radius: 5px;\n    color: #ffffff;\n    padding: 11px 30px;\n    display: block;\n    width: max-content;\n    text-decoration: none;\n}\n.host-banner-cont > img {\n    position: absolute;\n    top: 0;\n    right: 0;\n    object-fit: cover;\n    z-index: 9;\n    width: 100%;\n    height: 100%;\n    border-radius: 13px;\n}\n.host-banner-cont > .overlay {\n    background: rgba(0, 0, 0, 0.5);\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    z-index: 99;\n    border-radius: 13px;\n}\n.host-banner-timer-cont {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: 999;\n    padding: 26px;\n    max-width: 300px;\n    width: 100%;\n}\n.host-banner-timer-cont > .img-cont {\n    text-align: center;\n}\n.host-banner-timer-cont > .img-cont > img {\n    width: 32px;\n    height: 32px;\n    border-radius: 50